@import '../../../common/styles/sass/variables';
@import '../../../common/styles/sass/mixins';

.containerNew {
  @include setContainers();
  @include setDisplay(center, column);

  @media screen and (max-width: 700px) {
    width: 100%;
  }

  .text {
    width: 100%;
    height: 7%;
    position: absolute;
    top: 0;
    padding-top: 15px;
    background-color: $generalColor;
    text-align: center;
  }
}

